.App {
  text-align: center;
  display: flex;
}

.App-logo {
  height: 25vmin;
  pointer-events: none;
  padding-top: 0.01%;
  padding-left: 5%;
}

h1{
  font-size: calc(0.5px + 5vmin);
  text-shadow: grey 1px 2px;
  padding-top: calc(0.5px + 3vmin);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 1s linear  alternate;
  }
}

@keyframes App-logo-spin {
  from {
    transform: translate(0px, 0px);
  }
  to {
    transform: translate(5%, 0px);
  }
}
