.regList{
    background-color: #006600;
    height: calc(330px + 6vmin);
    width: calc(200px + 40vmin);
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-flow: column wrap;
    border-radius: 7px;
}

h2{
    text-align: center;
    color: #ffd700;
    font-size: calc(1px + 3.5vmin);
    margin-top: 20px;
    margin-bottom: 30px;
    text-shadow: grey 1px 1px;
}

input{
    margin-bottom: 25px;
    margin-left: 40px;
    margin-right: 40px;
    border: 0px;
}

button{
    margin-bottom: 30px;
    margin-left: 40px;
    margin-right: 40px;
}

.textInput{
    font-size: 18px;
}